#chessboard {
    display: grid;
    grid-template-columns: repeat(8, 100px);
    grid-template-rows: repeat(8, 100px);
    width: 800px;
    height: 800px;
    background-color: blue;
}
#timer {
    display:flex;
    justify-content: center;
   
    height: 50px;
    font-size: 36px;
    font-weight: bold;
    width: 800px;
    background-color: brown;
    font-size: 36px;
    font-weight: bold;
    color: white;
    border-radius: 10px;
    box-shadow: 5px 5px 10px #888888;
}

