#pawn-promotion-modal {
    position: absolute;
    top: calc(50% - 150px);
    left: calc(50% );
    transform: translate(-50%, -50%);
    display:flex;
    align-items:center;
    justify-content: space-around;
    background-color: rgba(0,0,0,.8);
    height: 300px;
    width: 800px;
}

#pawn-promotion-modal > img {
    height:200px;
    border-radius:50%;
}


#pawn-promotion-modal > img:hover {
    background-color: rgba(255,255,255,0.3);
    cursor:pointer;
}

.tile-highlight::before {
    content: " ";
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.4);
}



.GameOvermodal {
    display:flex;
    justify-content: center;
   
    height: 50px;
    font-size: 36px;
    font-weight: bold;
    width: 800px;
    background-color: White;
    font-size: 36px;
    font-weight: bold;
    color: Black;
    border-radius: 10px;
    box-shadow: 5px 5px 10px #888888;
  }
  
  .GameOvermodal-content {
    display:flex;
    justify-content: center;
   
    height: 50px;
    font-size: 36px;
    font-weight: bold;
    width: 800px;
    background-color: White;
    font-size: 36px;
    font-weight: bold;
    color: Black;
    border-radius: 10px;
    box-shadow: 5px 5px 10px #888888;
  }
  
  .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
